 opacity: 0.35;\n    content: '';\n    border-radius: inherit;\n    z-index: 1;\n    transition: opacity .2s;\n    pointer-events: none;\n    display: none;\n  }\n\n  .@{iconfont-css-prefix} {\n    transition: margin-left .3s @ease-in-out;\n  }\n\n  &&-loading:before {\n    display: block;\n  }\n\n  &&-loading:not(&-circle):not(&-circle-outline) {\n    padding-left: 29px;\n    pointer-events: none;\n    position: relative;\n    .@{iconfont-css-prefix} {\n      margin-left: -14px;\n    }\n  }\n\n  &-sm&-loading:not(&-circle):not(&-circle-outline) {\n    padding-left: 24px;\n    .@{iconfont-css-prefix} {\n      margin-left: -17px;\n    }\n  }\n\n  &-group {\n    .btn-group(@btn-prefix-cls);\n  }\n\n  &:not(&-circle):not(&-circle-outline)&-icon-only {\n    padding-left: 8px;\n    padding-right: 8px;\n  }\n\n  // http://stackoverflow.com/a/21281554/3040605\n  &:focus > span,\n  &:active > span {\n    position: relative;\n  }\n\n  // To ensure that a space will be placed between character and `Icon`.\n  > .@{iconfont-css-prefix} + span,\n  > span + .@{iconfont-css-prefix} {\n    margin-left: 0.5em;\n  }\n\n  &-clicked:after {\n    content: '';\n    position: absolute;\n    top: -1px;\n    left: -1px;\n    bottom: -1px;\n    right: -1px;\n    border-radius: inherit;\n    border: 0 solid @primary-color;\n    opacity: 0.4;\n    animation: buttonEffect .4s;\n    display: block;\n  }\n\n  &-danger&-clicked:after {\n    border-color: @btn-danger-color;\n  }\n\n  &-background-ghost {\n    background: transparent!important;\n    border-color: #fff;\n    color: #fff;\n  }\n\n  &-background-ghost&-primary {\n    .button-variant-ghost(@primary-color);\n  }\n\n  &-background-ghost&-danger {\n    .button-variant-ghost(@btn-danger-color);\n  }\n}\n\n@keyframes buttonEffect {\n  to {\n    opacity: 0;\n    top: -6px;\n    left: -6px;\n    bottom: -6px;\n    right: -6px;\n    border-width: 6px;\n  }\n}\n\n@primary-color: #1DA57A;","// mixins for button\n// ------------------------\n.butt